// app/page.tsx (Home)
import { getTopAnime, getPopularAnime } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import { HeroCarousel } from '@/components/hero-carousel'
import { AnimeGrid } from '@/components/anime-grid'
import { Suspense } from 'react'
import { SkeletonCarousel, SkeletonGrid } from '@/components/skeleton-loader'

async function Home() {
  const heroData = await getTopAnime(1)
  const heroAnimes = heroData?.data ? heroData.data.slice(0, 5) : []

  const popularData = await getPopularAnime(1)
  const popularAnimes = popularData?.data || []

  const trendingData = await getTopAnime(1)
  const trendingAnimes = trendingData?.data ? trendingData.data.slice(5, 25) : []

  return (
    <main className="min-h-screen bg-background">
      <Navbar />
      
      <Suspense fallback={<SkeletonCarousel />}>
        <HeroCarousel animes={heroAnimes} />
      </Suspense>

      <Suspense fallback={<SkeletonGrid />}>
        <AnimeGrid animes={popularAnimes} title="Popular Anime" />
      </Suspense>

      <Suspense fallback={<SkeletonGrid />}>
        <AnimeGrid animes={trendingAnimes} title="Trending This Week" />
      </Suspense>
    </main>
  )
}

export default Home
